.App{
  width:100%;
  height:100vh;
  padding-top: 10px;
  background-color: #1b1b32;
  font-family: Lato,sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.logo-container{
  width: 100%;
  height: 50px;
  margin: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo-img{
  height: 40px;
}
.calculator-container{
width: 400px;
height: 600px;
padding: 14px;
background-color: #99c9ff;
border-radius: 20px;
border: 5px solid white;
display: flex;
align-items: center;
flex-direction: column;
}
.row{
  margin: 4px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;

}